* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.content .darkbg {
  background: #000;
  position: absolute;
  z-index: 990;
}

.content .content-value {
  position: absolute;
  z-index: 999;
  top: 120px;
  left: 500px;
}

.content .content-value h1 {
  color: #e4a5a5;
  font-size: 50px;
  margin-bottom: 4rem;
}

.content .content-value strong {
  color: #8e9b16;
}

.content .content-value form input {
  max-width: 400px;
  width: 100%;
  height: 50px;
  font-size: 20px;
  border-radius: 15px;
  margin-bottom: 1rem;
  border: none;
  outline: none;
  padding: 0 10px;
}

.content .content-value button {
  width: 400px;
  height: 50px;
  border-radius: 30px;
  border: none;
  outline: none;
  font-size: 20px;
  font-weight: bolder;
  text-transform: uppercase;
  background: #b45133;
  color: #c79b9b;
  cursor: pointer;
}

.content .content-value button:hover {
  color: transparent;
  color: #e2b4b4;
  background-color: #d1603e;
}

.content .bg-video {
  max-width: 1800px;
  max-height: 500px;
}

.content .bg-video .the-video {
  width: 100%;
  height: 500px;
  -o-object-fit: cover;
     object-fit: cover;
}

.anime-list-section {
  padding: 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.anime-list-section .anime-card {
  margin: 1.5rem;
  border-radius: 10px;
  padding: 20px 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 550px;
  width: 250px;
  background: #a77575;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.anime-list-section .anime-card a {
  color: #f7dfdf;
  text-decoration: none;
}

.anime-list-section .anime-card a img {
  margin-bottom: 1rem;
}

.anime-list-section .anime-card a .card-desc {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  line-height: 25px;
}

.anime-list-section .anime-card:hover {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

