*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

.content{
            
    .darkbg{
        background: #000;
        position: absolute;
        z-index: 990;
    }
    
    .content-value{
        position: absolute;
        z-index: 999;
        top: 120px;
        left: 500px;

            h1{     
            color: #e4a5a5;
            font-size: 50px;
            margin-bottom: 4rem;
        }
            strong{
            color: #8e9b16;
        }

        form{
            input{
                max-width: 400px;
                width: 100%;
                height: 50px;
                font-size: 20px;
                border-radius: 15px;
                margin-bottom: 1rem;
                border: none;
                outline: none;
                padding: 0 10px;           
            }
        }
        button{
           width: 400px;
           height: 50px;
           border-radius: 30px;
           border: none;
           outline: none;
           font-size: 20px; 
           font-weight: bolder;
           text-transform: uppercase;
           background: #b45133;
           color: #c79b9b;
           cursor: pointer;
        }

        button:hover{
            color: transparent;
            color: #e2b4b4;
            background-color: #d1603e;
        }
    }

    
    
    .bg-video{
        max-width: 1800px;
        max-height: 500px;
                
        .the-video{
            width: 100%;
            height: 500px;
            object-fit: cover;
        }
     }
}

.anime-list-section{
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    
    .anime-card{
        margin: 1.5rem;
        border-radius: 10px;
        padding: 20px 10px;
        display: flex;
        justify-content: center;
        height: 550px;
        width: 250px;      
        background: #a77575;
        transition: all 0.5s ease;

        a{
          color: #f7dfdf;
          text-decoration: none;
        
            img{
            margin-bottom: 1rem;
            }
            
            .card-desc{
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                line-height: 25px;
            }
        }
    }
    .anime-card:hover{
        transition: all 0.5s ease;
        transform: scale(1.1); 
    }
}
          

@media screen and (max-width: 900px){

    .content{
        .content-value{
        position: absolute;
        z-index: 999;
        top: 50px;
        left: 130px;

            h1{     
            font-size: 15px;
            color: #e4a5a5;
            margin-bottom: 2rem;
        }
            strong{
            color: #8e9b16;
        }

        form{
            input{
                max-width: 120px;
                width: 100%;
                max-height: 20px;
                margin-bottom: 1rem;
                font-size: 10px;
            }
        }
        button{
           width: 120px;
           height: 20px;
           border-radius: 10px;
           text-decoration: none;
           font-size: 10px;
        }
    }

        .bg-video{
            .the-video{
             width: 100%;
             height: 100%;
             object-fit: cover;
            }
        }
    }        
    
    .anime-list-section{
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
    .anime-card{
        margin: 1.5rem;
        border-radius: 5px;
        padding: 10px;
        display: flex;
        justify-content: center;
        height: 440px;
        width: 150px;      
        background: #a77575; 

         a{
          color: #f7dfdf;
          text-decoration: none;
          font-size: 15px;
        
            img{
            max-width: 150px;
            width: 100%;
            max-height: 150px;
            height: 100%;
            margin-bottom: 1rem;
            }
            
            .card-desc{
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                line-height: 25px;
            }
        }   
    }
}
}

